import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import uniIcons from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			username: '',
			password: '',
			tabList: [{
					name: '老师',
					icon: 'cuIcon-comment'
				},
				{
					name: '学生',
					icon: 'cuIcon-dianhua'
				},
				{
					name: '家长',
					icon: 'cuIcon-wifi'
				},
				{
					name: '管理员',
					icon: 'cuIcon-wifi'
				}
			],
			userType: 0,
			school:''
		};
	},
	components: {
		WucTab,
		uniIcons,
	},
	computed: {},
	onLoad() {
		this.school = uni.getStorageSync('school')
	},
	methods: {
		toRegister() {
			uni.redirectTo({
				url: '/pages/register/register'
			});
		},
		tabChange(index) {
			this.userType = index;
		},
		signIn() {
			const {
				username,
				password,
				userType,
			} = this
			uni.showLoading({
				title: '登录中...'
			})
			// 调用云函数signin
			uniCloud.callFunction({
				name: 'signIn',
				data: {
					username,
					password,
					userType,
				},
			}).then((res) => {
				console.log('返回的', res);
				uni.hideLoading()
				if (res.result.status !== 0) {
					return Promise.reject(new Error(res.result.msg))
				}
				uni.setStorageSync('token', res.result.token)
				uni.setStorageSync('uid', res.result.uid)
				uni.setStorageSync("userType", this.userType)
				uni.showModal({
					content: '登录成功',
					showCancel: false
				})

				if (!res.result.class_id) {
					switch (this.userType){
						case 0:
						uni.navigateTo({
							url: '/pages/teacher_bind/teacher_bind'
						});
							break;
						case 1:
						uni.navigateTo({
							url: '/pages/student_bind/student_bind'
						})
							break;
						case 2:
						uni.navigateTo({
							url: '/pages/student_bind/student_bind'
						})
							break;
						case 3:
						uni.navigateTo({
							url: '/pages/administrator/admin_grade'
						})
							break;
					}
				} else {
					uni.setStorageSync("class_id", res.result.class_id)

					if (this.userType == 1 || this.userType == 2) {
						uni.setStorageSync("stu_num", res.result.stu_num)
						uni.setStorageSync("stu_name", res.result.stu_name)
					}

					uni.navigateTo({
						url: '/pages/index/index'
					});
				}
				


			}).catch((err) => {
				console.log(err);
				uni.hideLoading()
				uni.showModal({
					content: '登录失败，' + err.message,
					showCancel: false
				})
			})
		},
		loginMp() {
			uni.showLoading({
				title: '登录中...'
			})
			this.getCode().then((code) => {
				console.log('code', code);
				const {
					userType
				} = this;
				return uniCloud.callFunction({
					name: 'login',
					data: {
						code,
						userType,
					}
				})
			}).then((res) => {
				uni.hideLoading()
				console.log(res);
				if (res.result.status !== 0) {
					return Promise.reject(new Error(res.result.msg))
				}
				uni.setStorageSync('token', res.result.token)
				uni.showModal({
					content: '登录成功',
					showCancel: false
				})
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: '出现错误，请稍后再试.' + err.message,
					showCancel: false
				})
			})
		},
		getCode() {
			return new Promise((resolve, reject) => {
				uni.login({
					provider: 'weixin',
					success(e) {
						if (e.code) {
							resolve(e.code)
						} else {
							reject(new Error('微信登录失败'))
						}
					},
					fail(e) {
						reject(new Error('微信登录失败'))
					}
				})
			})
		},
		validateToken() {
			uni.showLoading({
				title: '加载中...'
			});
			uniCloud.callFunction({
				name: 'validateToken',
				data: {
					token: uni.getStorageSync('token')
				}
			}).then((res) => {
				console.log(res);
				uni.hideLoading()
				uni.showModal({
					content: res.result.msg,
					showCancel: false
				})
			}).catch((err) => {
				uni.hideLoading()
				uni.showModal({
					content: '请求云函数发生错误，' + err.message,
					showCancel: false
				})
			})
		},
	},
	onReady() {}
};
